.theme--light {
    --code-bg: #f5f2f0;
    --bg-color: var(--light-bg);
    --text-shadow: var(--light-bg-light-shadow);
    --menu-text-color: var(--g-text-color-light);
    --menu-text-hover-color: var(--primary-dark);
    --text-secondary: var(--g-text-color-disabled-light);
    --box-shadow-inset-small: inset -1px -1px 1px var(--light-bg-light-shadow),
    inset 1px 1px 1px var(--light-bg-dark-shadow);
}

.theme--dark {
    --code-bg: #272822;
    --bg-color: var(--dark-bg);
    --text-shadow: var(--dark-bg-dark-shadow);
    --menu-text-color: var(--g-text-color-dark);
    --menu-text-hover-color: var(--primary-light);
    --text-secondary: var(--g-text-color-disabled-dark);
    --box-shadow-inset-small: inset -1px -1px 1px var(--dark-bg-light-shadow),
    inset 1px 1px 1px var(--dark-bg-dark-shadow);
}

main {
    min-height: 100vh;
    min-width: 100vw;
    background: var(--bg-color);
}

.d-dummy {
    width: 100px;
    height: 24px;
}

.sidebar {
    top: -6px;
    z-index: 1;
    overflow: hidden;
    user-select: none;
    position: absolute;
    padding: 4px 16px;
    width: 250px !important;
    transform: translate3d(-324px, 0, 0);
    transition: transform 0.2s ease-in-out;
    height: calc(100vh - var(--padding) * 2 - var(--topbar-height) - 6px);
}

.sidebar--open {
    transform: translate3d(0, 0, 0);
}

.sidebar--always {
    position: unset !important;
    transform: translate3d(0, 0, 0) !important;
}

.sidebar ul {
    list-style: none;
    margin: 0;
}

.sidebar ul li {
    padding: 6px 12px;
    list-style: none;
    width: 220px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.sidebar ul li a,
.sidebar-menu-title a {
    text-decoration: none !important;
    color: var(--menu-text-color);
}

.sidebar-link-active,
.sidebar ul li a:hover,
.sidebar-menu-title:hover,
.sidebar-menu-title a:hover {
    color: var(--menu-text-hover-color) !important;
}

.sidebar-link-active {
    text-shadow: 1px 1px 1px var(--text-shadow);
}

.sidebar-menu-title {
    padding: 8px 0px;
    text-decoration: none;
}

.sidebar-menu {
    padding: 4px 0px;
}

.sidebar-menu input[type='checkbox'] {
    appearance: none;
    position: absolute;
    left: -9999;
}

.sidebar-menu input[type='checkbox'] ~ ul,
.sidebar-menu input[type='checkbox']:checked ~ ul {
    overflow: hidden;
    transition: all 0.25s;
}

.sidebar-menu input[type='checkbox'] ~ ul {
    max-height: 0;
}

.sidebar-menu input[type='checkbox']:checked ~ ul {
    max-height: calc(100vh - var(--padding) * 2 - var(--topbar-height) - 152px);
    overflow-y: scroll;
    transition: all 1s;
}

.sidebar-menu label {
    cursor: pointer;
}

.right-bar {
    width: 150px;
}

/* Main container */

.main-flex-card {
    width: 100%;
    display: flex;
    padding: 32px 0px;
    align-items: center;
    justify-content: center;
}


.main-container-sm {
    --padding: 8px;
    --topbar-height: 52px;
}

.main-content {
    display: flex;
    overflow: hidden;
    position: relative;
    align-items: flex-start;
    border-top-right-radius: 0px !important;
}

.main-view {
    flex-grow: 1;
    overflow: auto;
    padding: 8px 16px;
    will-change: transform;
    transition: transform 0.2s ease-in-out;
    height: calc(100vh - var(--padding) * 2 - var(--topbar-height) - 6px);
}

.main-view--open {
    opacity: 0.5;
    pointer-events: none;
    transform: translate3d(250px, 0, 0) !important;
}

.main-view--large {
    max-width: calc(100% - 250px - 150px);
}

.main-view--small {
    max-width: calc(100%) !important;
}

.main-view--home {
    max-width: calc(100% - 250px);
}

.main-topbar {
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
@media screen and (max-width: 800px) {
    .main-topbar {
        padding: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
@media screen and (min-width: 1200px) {
    .main-topbar {
        padding: 8px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.topbar-action,
.topbar-title {
    margin-left: 4px;
}

.topbar-title {
    font-weight: 400 !important;
}

/* Fitness app */

.fitness-app-container {
    width: 100%;
    margin: 0 auto;
}

.fitness-app-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.fitness-app {
    padding: 24px;
    position: relative;
    margin: 16px 32px;
}

.fitness-app--small {
    margin: 16px 0px;
}

.fitness-app-nav-bar {
    width: 300px;
    display: flex;
    left: 0px;
    bottom: -8px;
    margin: 8px 0px;
    padding: 16px 24px;
    position: absolute;
    box-shadow: var(--box-shadow);
    background: var(--bg-color);
    justify-content: space-between;
}

/* button doc */
.btn-doc-card {
    display: flex;
    padding: 32px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.doc-btn {
    margin: 32px;
}

/* code block */
.code-block-container {
    overflow: hidden;
    border-radius: 8px;
}
.code-block {
    overflow: auto;
    height: inherit;
    max-height: 300px;
    border-radius: 8px;
    will-change: height, max-height;
    transition: all 0.3s ease-in-out;
}

.code-block-closed {
    max-height: 0px;
    transition: all 0.3s ease-in-out;
}

/* clock */

.clock-app-toggle {
    position: absolute;
    bottom: 24px;
}

.clock-hand {
    z-index: 200;
    bottom: 50px;
    position: absolute;
    border-radius: 2px;
    transform-origin: bottom;
}

.clock-hours {
    width: 4px;
    height: 45px;
    background: grey;
}

.clock-minutes {
    width: 4px;
    height: 70px;
    background: grey;
}
.clock-seconds {
    width: 2px;
    height: 90px;
    background: var(--error);
}

.clock-point {
    width: 8px;
    height: 8px;
    z-index: 300;
    border-radius: 4px;
    background: grey;
}

.marker--1,
.marker--3 {
    width: 4px;
    height: 16px;
}
.marker--2,
.marker--4 {
    height: 4px;
    width: 16px;
}

.marker--1,
.marker--3,
.marker--2,
.marker--4 {
    border-radius: 2px;
    box-shadow: var(--box-shadow-inset-small);
}

.marker--1 {
    top: 2px;
}

.marker--2 {
    left: 2px;
}

.marker--3 {
    bottom: 2px;
}

.marker--4 {
    right: 2px;
}

.clock-dashed {
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='112' ry='112' stroke='grey' stroke-width='18' stroke-dasharray='1%2c4.5' stroke-dashoffset='25' stroke-linecap='butt'/%3e%3c/svg%3e") !important;
}

.clock-dashed--animating {
    animation: dash-animate 10s linear infinite;
}

@keyframes dash-animate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
